inear;\n                        display: flex;\n                        align-items: center;\n                        .ScoresBar-addedScore {\n                            color: #fff;\n                            font-size: 14px;\n                            font-weight: 700;\n                            white-space: nowrap;\n                            max-width: 100%;\n                        }\n                        .ScoresBar-scores {\n                            position: absolute;\n                            transform: translateX(calc(10px + 100%));\n                            line-height: 16px;\n                            font-size: 16px;\n                            font-weight: 700,\n                        }\n                    }\n                }\n            }\n            .yaxes{\n                position: sticky;\n                top: 0px;\n            }\n            .LB-YAxes {\n                $color: #562b0866;\n                position: absolute;\n                height: 100%;\n                width: 0px;\n                border-left: 1px dashed $color;\n                transition:  left 0.7s linear;\n                > span {\n                    color: $color;\n                    transform: translateX(50%) translateY(-100%);\n                    position: absolute;\n                }\n            }\n        }\n        .LB-DiagramVersionsDisplay {\n            width: 100%;\n            height: 100px;\n            position: relative;\n            padding: 12px 0px;\n            margin-bottom: 8px;\n            margin-left: 0;\n            transform: translateX(0);\n            transition: transform 1s linear;\n            &.emptyVersions {\n                transform: translateX(50%);\n            }\n            .spaceKeeper {\n                height: 74px;\n                position: relative;\n                z-index: -1;\n            }\n            .DiagramVersionsDisplay-versionWrapper {\n                position: absolute;\n              